  bottom: 10%; 
  left: 15%; 
  width: 250px; 
  height: 250px; 
  background: var(--md-sys-color-secondary-container);
  animation-delay: 10s; 
}

.shape-4 { 
  bottom: 20%; 
  right: 20%; 
  width: 180px; 
  height: 180px; 
  background: var(--md-sys-color-success-container);
  animation-delay: 15s; 
}

/* Form input styling with Material Design 3 */
.form-input {
  background-color: var(--md-sys-color-surface-container);
  color: var(--md-sys-color-on-surface);
  border: 1px solid var(--md-sys-color-outline);
  border-radius: 12px;
  padding: 16px 20px;
  font-size: 16px;
  transition: all 0.2s ease-in-out;
  width: 100%;
}

.form-input:focus {
  border-color: var(--md-sys-color-primary);
  background-color: var(--md-sys-color-surface-container-high);
  outline: none;
  box-shadow: 0 0 0 2px rgba(103, 80, 164, 0.2);
}

.form-input::-moz-placeholder {
  color: var(--md-sys-color-on-surface-variant);
  opacity: 1;
}

.form-input::placeholder {
  color: var(--md-sys-color-on-surface-variant);
  opacity: 1;
}

/* Auto-fill protection */
.form-input:-webkit-autofill,
.form-input:-webkit-autofill:hover,
.form-input:-webkit-autofill:focus,
.form-input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px var(--md-sys-color-surface-container) inset !important;
  -webkit-text-fill-color: var(--md-sys-color-on-surface) !important;
}

/* Status indicators */
.status-success {
  background-color: var(--md-sys-color-success-container);
  color: var(--md-sys-color-on-success-container);
  border-left: 4px solid var(--md-sys-color-success);
}

.status-error {
  background-color: var(--md-sys-color-error-container);
  color: var(--md-sys-color-on-error-container);
  border-left: 4px solid var(--md-sys-color-error);
}

.status-warning {
  background-color: var(--md-sys-color-warning-container);
  color: var(--md-sys-color-on-warning-container);
  border-left: 4px solid var(--md-sys-color-warning);
}

/* Loading states */
.loading-spinner {
  width: 24px;
  height: 24px;
  border: 2px solid var(--md-sys-color-outline-variant);
  border-top: 2px solid var(--md-sys-color-primary);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive design breakpoints */
@media (max-width: 768px) {
  .auth-form {
    margin: 16px;
    border-radius: 20px;
  }
  
  .floating-shape {
    width: 150px !important;
    height: 150px !important;
  }
}

/* Focus management for accessibility */
.focus-ring:focus {
  outline: 2px solid var(--md-sys-color-primary);
  outline-offset: 2px;
  border-radius: 4px;
}

/* Smooth animations */
.animate-fade-in {
  animation: fadeIn 0.6s ease-out;
}

.animate-slide-up {
  animation: slideUp 0.6s ease-out;
}

.animate-scale-in {
  animation: scaleIn 0.4s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { 
    opacity: 0; 
    transform: translateY(30px); 
  }
  to { 
    opacity: 1; 
    transform: translateY(0); 
  }
}

@keyframes scaleIn {
  from { 
    opacity: 0; 
    transform: scale(0.9); 
  }
  to { 
    opacity: 1; 
    transform: scale(1); 
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Animation utilities */
.animate-slideIn {
  animation: slideIn 0.3s ease-out forwards;
}

.animate-fadeIn {
  animation: fadeIn 0.2s ease-out forwards;
}

.animate-slideUp {
  animation: slideUp 0.3s cubic-bezier(0.16, 1, 0.3, 1) forwards;
}

.animate-fadeInUp {
  animation: fadeInUp 0.4s cubic-bezier(0.16, 1, 0.3, 1) forwards;
}

.animate-slide-down {
  animation: slideDown 0.3s ease-out forwards;
}

/* Hover effects */
.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
